<template>
  <ul class="sidebar-menu pos-r" ref="scrollable" :class="{'rtl': rtl}">
    <li class="nav-item mT-30 active" :class="{'ta-r': rtl}">
      <a class="sidebar-link" href="#/user/gyms">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-blue-500 ti-home"></i>
        </span>
        <span class="title">{{t('HOME')}}</span>
      </a>
    </li>
    <li class="nav-item" :class="{'ta-r': rtl}">
      <a class='sidebar-link' href="#/user/purchases">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-deep-orange-500 ti-shopping-cart"></i>
        </span>
        <span class="title">{{t('PURCHASES')}}</span>
      </a>
    </li>
    <li class="nav-item" :class="{'ta-r': rtl}">
      <a class='sidebar-link' href="#/user/profile">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-light-blue-500 ti-user"></i>
        </span>
        <span class="title">{{t('PROFILE')}}</span>
      </a>
    </li>
    <li class="nav-item" :class="{'ta-r': rtl}">
      <a class='sidebar-link' href="#/user/admin">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-brown-500 fa fa-briefcase"></i>
        </span>
        <span class="title">{{t('ADMIN_PAGE')}}</span>
      </a>
    </li>
    <li class="nav-item" :class="{'ta-r': rtl}">
      <a class='sidebar-link' href="#/login">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-deep-purple-500 fa fa-user"></i>
        </span>
        <span class="title">{{t('LOGIN')}}</span>
      </a>
    </li>
    <li class="nav-item" :class="{'ta-r': rtl}">
      <a class='sidebar-link' href="#/signup">
        <span :class="{'icon-holder': !rtl, 'icon-holder-rtl': rtl}">
          <i class="c-green-500 fa fa-user-plus"></i>
        </span>
        <span class="title">{{t('REGISTER')}}</span>
      </a>
    </li>
  </ul>
</template>
<script>
import { mapGetters } from 'vuex'
import PerfectScrollbar from 'perfect-scrollbar'
import ScrollConfig from '@/configs/ScrollConfig'
import * as $ from 'jquery'
export default {
  name: 'SidebarMenu',
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  mounted () {
    // Add Custom Scroll
    new PerfectScrollbar(this.$refs.scrollable, ScrollConfig)
    $('.sidebar .sidebar-menu li a').on('click', function () {
      const $this = $(this)

      if ($this.parent().hasClass('open')) {
        $this
          .parent()
          .children('.dropdown-menu')
          .slideUp(200, () => {
            $this.parent().removeClass('open')
          })
      } else {
        $this
          .parent()
          .parent()
          .children('li.open')
          .children('.dropdown-menu')
          .slideUp(200)

        $this
          .parent()
          .parent()
          .children('li.open')
          .children('a')
          .removeClass('open')

        $this
          .parent()
          .parent()
          .children('li.open')
          .removeClass('open')

        $this
          .parent()
          .children('.dropdown-menu')
          .slideDown(200, () => {
            $this.parent().addClass('open')
          })
      }
    })
  }
}
</script>
<style lang="scss">
</style>



