<template>
  <ul class="nav-left" ref="NavLeft">
    <!-- toggle application navigation -->
    <li v-if="!rtl">
      <a id='sidebar-toggle' class="sidebar-toggle" href="javascript:void(0)" @click="userPageApplicationSidebarManage({type: 'toggle'})">
        <i class="ti-menu"></i>
      </a>
    </li>
    <!-- toggle Gym Search -->
    <li v-if="!sidebarIsOpen">
      <a class="sidebar-toggle d-n@md+"
         id='chat-sidebar-toggle'
         @click="userPageSidebarManage({type: 'set', payload: true})"
         href="javascript:void(0)">
        <i class="ti-arrow-left"></i>
      </a>
    </li>
    <!-- search -->
    <li class="search-box">
      <a class="search-toggle no-pdd-right" href="javascript:void(0)">
        <i class="search-icon ti-search pdd-right-10"></i>
        <i class="search-icon-close ti-close pdd-right-10"></i>
      </a>
    </li>
    <li class="search-input">
      <input class="form-control" type="text" placeholder="Search...">
    </li>
  </ul>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import * as $ from 'jquery'
export default {
  name: 'NavLeft',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl',
      'sidebarIsOpen': 'getUserPageSidebarIsOpen'
    })
  },
  data () {
    return {
    }
  },
  methods: {
    ...mapActions([
      'userPageApplicationSidebarManage',
      'userPageSidebarManage'
    ])
  },
  mounted () {
    this.$nextTick(function () {
      $(this.$refs.NavLeft).find('.search-toggle').on('click', e => {
        $('.search-box, .search-input').toggleClass('active')
        $('.search-input input').focus()
        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



