<template>
  <div class="bgc-white p-20 bd">
    <h6 class="c-grey-900">{{t('ADDRESS')}}:</h6>
    <div class="mT-30">
      <form>
        <div class="form-group">
          <label for="exampleInputEmail2">{{t('ADDRESS')}}:</label>
          <textarea class="form-control" id="exampleInputEmail2" aria-describedby="emailHelp" :placeholder="t('ADDRESS')"></textarea>
          <small id="emailHelp" class="form-text text-muted">help content.</small>
        </div>
      </form>
    </div>

    <div class="layer w-100">
      <div id="google-map-edit-address" ref="googleMap" style="padding-bottom: 75%;"></div>
    </div>

    <button type="submit" class="btn btn-primary mT-30">{{t('SUBMIT')}}</button>
  </div>
</template>
<script>
import loadGoogleMapsAPI from 'load-google-maps-api'
import MapConfig from '@/configs/MapConfig'
export default {
  name: 'GymEditAddress',
  components: {
  },
  data () {
    return {
    }
  },
  mounted () {
    loadGoogleMapsAPI({
      key: 'AIzaSyDW8td30_gj6sGXjiMU0ALeMu1SDEwUnEA'
    }).then(() => {
      const latitude = 26.8206
      const longitude = 30.8025
      const { google } = window
      const mapOptions = Object.assign({}, MapConfig, {
        center: new google.maps.LatLng(latitude, longitude),
        mapTypeId: google.maps.MapTypeId.ROADMAP
      })

      const map = new google.maps.Map(this.$refs.googleMap, mapOptions)

      new google.maps.Marker({
        map,
        position: new google.maps.LatLng(latitude, longitude),
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        visible: true
      })
    })
  }
}
</script>
<style>

</style>



