<template>
  <div class="peers fxw-nw ai-c p-20 bdB bgc-white bgcH-grey-50 cur-p"
       :class="{'rtl': rtl}"
       @click="userPageSidebarManage({type: 'set', payload: false})">
    <div class="peer">
      <img src="@/assets/static/images/gym-1.jpg" alt="" class="w-3r h-3r bdrs-50p">
    </div>
    <!-- c-amber-500 , c-grey-500 , c-red-500 -->
    <div class="peer peer-greed" :class="{'ta-r': rtl, 'pR-20': rtl, 'pL-20': !rtl}">
      <h6 class="mB-0 lh-1 fw-400">{{iname}}</h6>
      <small class="lh-1 c-green-500">{{t(istatus)}}</small>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'Item',
  components: {
  },
  props: {
    iname: {
      type: String,
      required: true
    },
    istatus: {
      type: String,
      default: 'AVAILABLE'
    },
    iimg: {
      type: String,
      default: '@/assets/static/images/gym-1.jpg'
    }
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  methods: {
    ...mapActions([
      'userPageSidebarManage'
    ])
  }
}
</script>
<style>

</style>



