<template>
  <div class="peer bdR"
       :class="{'open': sidebarIsOpen}"
       id="chat-sidebar"
       style="height: calc(100vh - 65px)">
    <div class="layers h-100">
      <!-- Search -->
      <s-search></s-search>

      <!-- List -->
      <div class="layer w-100 fxg-1 pos-r" ref="scrollable">
        <s-item
          :key="index"
          v-for="(item, index) in [1,2,3,4,5,6,7]"
          :iname="item + 'Gym'"
          istatus="AVAILABLE">
        </s-item>
      </div>
    </div>
  </div>
</template>
<script>
import Search from './Sidebar/Search'
import Item from './Sidebar/Item'
import PerfectScrollbar from 'perfect-scrollbar'
import ScrollConfig from '@/configs/ScrollConfig'

import { mapGetters } from 'vuex'
export default {
  name: 'Sidebar',
  components: {
    's-search': Search,
    's-item': Item
  },
  mounted () {
    // Add Custom Scroll
    new PerfectScrollbar(this.$refs.scrollable, ScrollConfig)
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'sidebarIsOpen': 'getUserPageSidebarIsOpen'
    })
  }
}
</script>
<style>

</style>



