<template>
  <main class='main-content bgc-grey-100' :class="{'rtl ta-r': rtl}">
    <div id='mainContent'>
      <div class="bgc-grey-100">
        <div class="peers">
          <div class="layer peer-greed p-10 w-50p@md+">
            <setting></setting>
          </div>
          <div class="layer p-10 peer-greed w-50p@md+">
            <last-purchases></last-purchases>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import { mapGetters } from 'vuex'
import LastPurchases from './UserProfile/LastPurchases'
import Setting from './UserProfile/Setting'
export default {
  name: 'UserProfile',
  components: {
    'last-purchases': LastPurchases,
    'setting': Setting
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



