<template>
  <div class="layer w-100" :class="{'rtl': rtl}">
    <div class="peers ai-c jc-sb fxw-nw">
      <div class="peer peer-greed">
        <div class="layers">
          <!-- Temprature -->
          <div class="layer w-100">
            <div class="peers fxw-nw ai-c">
              <div class="peer" :class="{'mR-20': !rtl, 'mL-20': rtl}">
                <h3>08:00 {{t('TO')}} 17:00</h3>
              </div>
              <div class="peer">
                <span class="icon-holder">
                  <!-- closedColor class -->
                  <i class="c-green-500 ti-check fsz-md"></i>
                </span>
              </div>
            </div>
          </div>

          <!-- Condition -->
          <div class="layer w-100" :class="{'ta-r': rtl}">
            <span class="c-grey-600" :class="{'fw-600': !rtl}">
                {{t('THIS_GYM_IS_OPEN')}}!
            </span>
          </div>
        </div>
      </div>
      <div class="peer">
        <div class="layers ai-fe">
          <div class="layer">
            <h5 class="mB-5">{{t('MON')}}</h5>
          </div>
          <div class="layer">
            <span class="c-grey-600" :class="{'fw-600': !rtl}">Nov, 01 2017</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'GymShortOverview',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



