<template>
  <li class="notifications dropdown" ref="Basket" :class="{'rtl': rtl}">
    <span class="counter bgc-red">3</span>
    <a href="" class="dropdown-toggle no-after" data-toggle="dropdown">
      <i class="ti-shopping-cart"></i>
    </a>

    <ul class="dropdown-menu">
      <li class="pX-20 pY-15 bdB" :class="{'ta-r': rtl}">
        <i class="ti-shopping-cart" :class="{'pR-10': !rtl, 'pL-10': rtl}"></i>
        <span class="fsz-sm c-grey-900" :class="{'fw-600': !rtl}">
          {{t('BASKET')}}
        </span>
      </li>
      <li>
        <ul class="ovY-a pos-r lis-n p-0 m-0 fsz-sm" ref="scrollable">
          <li>
            <b-item iname="مهرداد"></b-item>
          </li>
        </ul>
      </li>
      <li class="pX-20 pY-15 ta-c bdT">
        <span>
          <a href="#/user/purchases" class="c-grey-600 cH-blue fsz-sm td-n">{{t('VIEW_ALL')}} {{t('PURCHASES')}}<i class="ti-angle-right fsz-xs mL-10" :class="{'ti-angle-right mL-10': !rtl, 'ti-angle-left mR-10': rtl}"></i>
          </a>
        </span>
      </li>
    </ul>
  </li>
</template>
<script>
import { mapGetters } from 'vuex'
import * as $ from 'jquery'
import ScrollConfig from '@/configs/ScrollConfig'
import PerfectScrollbar from 'perfect-scrollbar'

// components
import Item from './Basket/Item'
export default {
  name: 'Basket',
  components: {
    'b-item': Item
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  mounted () {
    // Add Custom Scroll
    new PerfectScrollbar(this.$refs.scrollable, ScrollConfig)
    this.$nextTick(function () {
      $(this.$refs.Basket).find('.dropdown-toggle').click(function (e) {
        $(this).parent().toggleClass('show')
        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



