<template>
  <a href="" class='peers fxw-nw td-n p-20 bdB c-grey-800 cH-blue bgcH-grey-100' :class="{'rtl': rtl}">
    <div class="peer" :class="{'mR-15': !rtl, 'mL-15': rtl}">
      <img class="w-3r bdrs-50p" src="https://randomuser.me/api/portraits/men/1.jpg" alt="">
    </div>
    <div class="peer peer-greed" :class="{'ta-r': rtl}">
      <span>
        <span class="c-grey-600">{{t('BUY')}} <span class="text-dark">5</span> {{t('VISIT')}} {{t('FROM')}}

        <span class="fw-500">{{iname}}</span>
      </span>
    </span>
    <p class="m-0" :class="{'ta-r': rtl}">
      <small class="fsz-xs">5/6/2018 18:30</small>
    </p>
  </div>
</a>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Item',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  }
}
</script>
<style>

</style>



