<template>
  <div class="peers ai-s fxw-nw h-100vh" :class="{'rtl ta-r': rtl}">
     <!--  -->
    <div class="d-n@sm- peer peer-greed h-100 pos-r bgr-n bgpX-c bgpY-c bgsz-cv login-bg">
      <div class="pos-a centerXY">
        <div class="bgc-white bdrs-50p pos-r" style='width: 120px; height: 120px;'>
          <img class="pos-a centerXY" src="/static/img/logo.png" alt="">
        </div>
      </div>
    </div>
    <div class="col-12 col-md-4 peer pX-40 pY-80 h-100 bgc-white scrollable pos-r" style='min-width: 320px;'>
      <h4 class="fw-300 c-grey-900 mB-40">{{t('LOGIN')}}</h4>
      <form>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('USERNAME')}}</label>
          <input type="email" class="form-control" :placeholder="t('TYPE_YOUR_USERNAME')">
        </div>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('PASSWORD')}}</label>
          <input type="password" class="form-control" :placeholder="t('TYPE_YOUR_PASSWORD')">
        </div>
        <div class="form-group">
          <div class="peers ai-c jc-sb fxw-nw">
            <div class="peer">
              <div class="checkbox checkbox-circle checkbox-info peers ai-c" :class="{'checkbox-rtl': rtl}">
                <input type="checkbox" id="inputLoginRememberMe" name="inputCheckboxesCall" class="peer">
                <label for="inputLoginRememberMe" class="peers peer-greed js-sb ai-c">
                  <span class="peer peer-greed">{{t('REMEMBER_ME')}}</span>
                </label>
              </div>
            </div>
            <div class="peer">
              <a href="#/user/gyms" class="btn btn-primary">{{t('LOGIN')}}</a>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Login',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style lang="scss">
.login-bg {
  background-image: url('/static/img/bg.jpg');
}
</style>



