<template>
  <div class="peers ai-s fxw-nw h-100vh" :class="{'rtl ta-r': rtl}">
    <div class="peer peer-greed h-100 pos-r bgr-n bgpX-c bgpY-c bgsz-cv" style='background-image: url("/static/img/bg.jpg")'>
      <div class="pos-a centerXY">
        <div class="bgc-white bdrs-50p pos-r" style='width: 120px; height: 120px;'>
          <img class="pos-a centerXY" src="@/assets/static/images/logo.png" alt="">
        </div>
      </div>
    </div>
    <div class="col-12 col-md-4 peer pX-40 pY-80 h-100 bgc-white scrollable pos-r" style='min-width: 320px;'>
      <h4 class="fw-300 c-grey-900 mB-40">{{t('REGISTER')}}</h4>
      <form>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('USERNAME')}}</label>
          <input type="text" class="form-control" Placeholder='John Doe'>
        </div>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('ADDRESS')}} {{t('EMAIL')}}</label>
          <input type="email" class="form-control" Placeholder='name@email.com'>
        </div>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('PASSWORD')}}</label>
          <input type="password" class="form-control" :placeholder="t('PASSWORD')">
        </div>
        <div class="form-group">
          <label class="text-normal text-dark">{{t('CONFIRM')}} {{t('PASSWORD')}}</label>
          <input type="password" class="form-control" :placeholder="t('PASSWORD')">
        </div>
        <div class="form-group">
          <a href="#/user/gyms" class="btn btn-primary">{{t('REGISTER')}}</a>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'SignUp',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



