<template>
  <main class='main-content bgc-grey-100' :class="{'rtl ta-r': rtl}">
    <div id='mainContent'>
      <div class="bgc-grey-100">
        <div class="peers">
          <div class="layer p-10 peer-greed w-50p@md+">
            <div>
              <gym-sells></gym-sells>
            </div>
            <div class="mT-20">
              <gym-comments></gym-comments>
            </div>

            <div class="mT-20">
              <gym-edit-profile-images></gym-edit-profile-images>
            </div>
          </div>
          <div class="layer peer-greed p-10 w-50p@md+">
            <div>
              <gym-facilities></gym-facilities>
            </div>

            <div class="mT-20">
              <gym-edit-description></gym-edit-description>
            </div>

            <div class="mT-20">
              <gym-edit-address></gym-edit-address>
            </div>

            <div class="mT-20">
              <gym-edit-hours></gym-edit-hours>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import { mapGetters } from 'vuex'
import GymFacilities from './UserAdminPage/GymFacilities'
import GymSells from './UserAdminPage/GymSells'
import GymComments from './UserAdminPage/GymComments'
import GymEditDescription from './UserAdminPage/GymEditDescription'
import GymEditAddress from './UserAdminPage/GymEditAddress'
import GymEditProfileImages from './UserAdminPage/GymEditProfileImages'
import GymEditHours from './UserAdminPage/GymEditHours'
export default {
  name: 'UserAdminPage',
  components: {
    'gym-facilities': GymFacilities,
    'gym-sells': GymSells,
    'gym-comments': GymComments,
    'gym-edit-description': GymEditDescription,
    'gym-edit-address': GymEditAddress,
    'gym-edit-profile-images': GymEditProfileImages,
    'gym-edit-hours': GymEditHours
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



