<template>
  <main class='main-content bgc-grey-100'>
    <div id='mainContent'>
      <div :class="{'full-container': !rtl, 'full-container-rtl rtl': rtl}">
        <div class="peers fxw-nw pos-r">
          <!-- Sidebar -->
          <a-sidebar></a-sidebar>

          <!-- Gym Profile -->
          <gym-profile></gym-profile>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import { mapGetters } from 'vuex'
import Sidebar from './UserGyms/Sidebar'
import GymProfile from './UserGyms/GymProfile'
export default {
  name: 'UserGyms',
  components: {
    'a-sidebar': Sidebar,
    'gym-profile': GymProfile
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style lang="scss">

</style>



