<template>
  <ul class="nav-right" ref="navRight">
    <n-basket></n-basket>
    <n-user :iname="'مهرداد'"></n-user>
    <li v-if="rtl">
      <a id='sidebar-toggle' class="sidebar-toggle" href="javascript:void(0)" @click="userPageApplicationSidebarManage({type: 'toggle'})">
        <i class="ti-menu"></i>
      </a>
    </li>
  </ul>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
// components
import Basket from './NavRight/Basket'
import User from './NavRight/User'
export default {
  name: 'NavRight',
  components: {
    'n-basket': Basket,
    'n-user': User
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  mounted () {
  },
  methods: {
    ...mapActions([
      'userPageApplicationSidebarManage'
    ])
  }
}
</script>
<style>

</style>



