<template>
  <div class="layer w-100 mB-20" :class="{'rtl': rtl}">
    <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('OVERVIEW')}} {{t('ABOUT')}} {{iname}}:</h6>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'GymTitle',
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  },
  components: {
  },
  mounted () {
  }
}
</script>
<style>

</style>



