<template>
  <div class="briefOverview p-10">
    <!-- #Weather ==================== -->
    <div class="bgc-white p-20">
      <div class="layers">
        <!-- Gym Title -->
        <gym-title :iname="'مهرداد'"></gym-title>

        <!-- Gym Short Overview -->
        <gym-short-overview></gym-short-overview>

        <!-- Gym Facilities -->
        <gym-facilities></gym-facilities>

        <!-- Week Forecast -->
        <gym-hours></gym-hours>
      </div>
    </div>
  </div>
</template>
<script>
import GymTitle from './GymOverView/GymTitle'
import GymShortOverview from './GymOverView/GymShortOverview'
import GymFacilities from './GymOverView/GymFacilities'
import GymHours from './GymOverView/GymHours'
export default {
  name: 'GymOverView',
  components: {
    'gym-title': GymTitle,
    'gym-short-overview': GymShortOverview,
    'gym-facilities': GymFacilities,
    'gym-hours': GymHours
  },
  data () {
    return {
    }
  }
}
</script>
