const actions = {
  userPageApplicationSidebarManage: (context, payload) => {
    switch (payload.type) {
      case 'toggle':
        context.commit('userPageAppIsCollapsedToggle')
        break
      case 'set':
        context.commit('userPageAppIsCollapsedSet', {payload: payload.payload})
        break
    }
  },
  userPageSidebarManage: (context, payload) => {
    switch (payload.type) {
      case 'toggle':
        context.commit('userPageSidebarIsOpenToggle')
        break
      case 'set':
        context.commit('userPageSidebarIsOpenSet', {payload: payload.payload})
        break
    }
  }
}

export default actions



