<template>
  <div class="ltr" :class="{'page-container': !rtl, 'page-container-rtl': rtl}">
    <!-- ### $overlay ### -->
    <div v-if="getUserPageAppIsCollapsed" class="overlay" @click="userPageApplicationSidebarManage({type: 'set', payload: false})"></div>

    <!-- ### $Topbar ### -->
    <top-bar></top-bar>

    <!-- ### $App Screen Content ### -->
    <router-view></router-view>
  </div>
</template>
<script>
import TopBar from './main/TopBar'
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'Main',
  components: {
    'top-bar': TopBar
  },
  methods: {
    ...mapActions([
      'userPageApplicationSidebarManage'
    ])
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl',
      'getUserPageAppIsCollapsed': 'getUserPageAppIsCollapsed'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style lang="scss" scoped>
  .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: transparent;
    z-index: 900;
  }
</style>



