<template>
  <div id="app" class="app" :class="getUserPageAppIsCollapsedClass">
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  computed: {
    ...mapGetters([
      'getUserPageAppIsCollapsedClass'
    ])
  },
  mounted () {
    this.$translate.setLang('farsi')
  }
}
</script>

<style lang="scss">
.checkbox.checkbox-circle label::before {
  border: 1px solid #72777a;
}
</style>



