const mutations = {
  rtlSet: (state, payload) => {
    state.rtl = payload.payload
  },
  userPageAppIsCollapsedSet: (state, payload) => {
    state.userPageAppIsCollapsed = payload.payload
  },
  userPageAppIsCollapsedToggle: (state, payload) => {
    state.userPageAppIsCollapsed = !state.userPageAppIsCollapsed
  },
  userPageSidebarIsOpenSet: (state, payload) => {
    state.userPageSidebarIsOpen = payload.payload
  },
  userPageSidebarIsOpenToggle: (state, payload) => {
    state.userPageSidebarIsOpen = !state.userPageSidebarIsOpen
  }
}

export default mutations



