import fa from '@/lang/fa'
const lang = {
  language: {
    search: fa['SEARCH'] + ' ' + fa['IN'] + ' ' + fa['PURCHASES'] + ':',
    'info': `${fa['SHOWING']} _START_ ${fa['TO']} _END_ ${fa['FROM']} _TOTAL_ ${fa['ENTRIES']}`,
    'infoEmpty': `${fa['SHOWING']} 0 ${fa['TO']} 0 ${fa['FROM']} 0 ${fa['ENTRIES']}`,
    'paginate': {
      'first': fa['FIRST'],
      'last': fa['LAST'],
      'next': fa['NEXT'],
      'previous': fa['PREVIOUS']
    },
    'infoFiltered': `، ${fa['FILTERED']} ${fa['FROM']} _MAX_ ${fa['ENTRIES']}`
  }
}
export default lang



