<template>
  <!-- Gym Profile -->
  <div class="peer peer-greed" id='chat-box' style="height: calc(100vh - 65px)">
    <div class="layers h-100">
      <div class="layer w-100 fxg-1 bgc-grey-200 pos-r" ref="scrollable">
        <gym-buy-options></gym-buy-options>

        <!-- Gym overview -->
        <gym-overview></gym-overview>

        <div class="pX-10 pB-10">
          <div class="peers bg-white">
            <div class="layer peer-greed w-50p@md+">
              <!-- Gym Gallery -->
              <gym-gallery :iname="'مهرداد'"></gym-gallery>
            </div>
            <div class="layer peer-greed w-50p@md+">
              <!-- Gym Location (map) -->
              <gym-location></gym-location>
            </div>
          </div>
        </div>
        <div class="p-10">
          <gym-people-comments :iname="'مهرداد'"></gym-people-comments>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// modules
import PerfectScrollbar from 'perfect-scrollbar'
import ScrollConfig from '@/configs/ScrollConfig'

// components
import GymOverview from './GymProfile/GymOverview'
import GymGallery from './GymProfile/GymGallery'
import GymLocation from './GymProfile/GymLocation'
import GymPeopleComments from './GymProfile/GymPeopleComments'
import GymBuyOptions from './GymProfile/GymBuyOptions'
export default {
  name: 'GymProfile',
  components: {
    'gym-overview': GymOverview,
    'gym-gallery': GymGallery,
    'gym-location': GymLocation,
    'gym-people-comments': GymPeopleComments,
    'gym-buy-options': GymBuyOptions
  },
  mounted () {
    new PerfectScrollbar(this.$refs.scrollable, ScrollConfig)
  }
}
</script>
<style>

</style>



