import state from './ApplicationModule/state'
import actions from './ApplicationModule/actions'
import getters from './ApplicationModule/getters'
import mutations from './ApplicationModule/mutations'

const ApplicationModule = {
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
}

export default ApplicationModule



