<template>
  <div class="layer p-20 bg-white">
    <div class="layer w-100 mB-10">
      <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('LOCATION')}}:</h6>
    </div>
    <div class="layer w-100 mB-20" :class="{'ta-r': rtl}">
      <span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Explicabo ea veniam unde at tempora, vero, ad quasi non dolores, aliquam atque nostrum. A deleniti qui in. Tenetur ullam reprehenderit pariatur.</span>
    </div>
    <div class="layer w-100">
      <div id="google-map" ref="googleMap" style="padding-bottom: 75%;"></div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import loadGoogleMapsAPI from 'load-google-maps-api'
import MapConfig from '@/configs/MapConfig'
export default {
  name: 'GymLocation',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  mounted () {
    loadGoogleMapsAPI({
      key: 'AIzaSyDW8td30_gj6sGXjiMU0ALeMu1SDEwUnEA'
    }).then(() => {
      const latitude = 26.8206
      const longitude = 30.8025
      const { google } = window
      const mapOptions = Object.assign({}, MapConfig, {
        center: new google.maps.LatLng(latitude, longitude),
        mapTypeId: google.maps.MapTypeId.ROADMAP
      })

      const map = new google.maps.Map(this.$refs.googleMap, mapOptions)

      new google.maps.Marker({
        map,
        position: new google.maps.LatLng(latitude, longitude),
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        visible: true
      })
    })
  }
}
</script>
<style>

</style>



