const state = {
  getRtl: state => { // whole page direction
    return state.rtl
  },
  getUserPageAppIsCollapsed: state => {
    return state.userPageAppIsCollapsed
  },
  getUserPageAppIsCollapsedClass: state => { // sdiebar toggle effect on .app with isCollapsed class
    // if is rtl
    if (state.rtl && state.userPageAppIsCollapsed) return 'is-collapsed-rtl'
    // if is ltr
    else if (!state.rtl && state.userPageAppIsCollapsed) return 'is-collapsed'
    // default class
    else return ''
  },
  getUserPageSidebarIsOpen: state => {
    return state.userPageSidebarIsOpen
  }
}

export default state



