<template>
  <div class="layer p-20 bg-white" :class="{'rtl': rtl}" ref="GymGalley">
    <div class="layer w-100 mB-10">
      <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('DESCRIPTION')}} {{t('ABOUT')}} {{iname}}:</h6>
    </div>
    <div class="layer w-100 mB-20" :class="{'ta-r': rtl}">
      <span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Explicabo ea veniam unde at tempora, vero, ad quasi non dolores, aliquam atque nostrum. A deleniti qui in. Tenetur ullam reprehenderit pariatur.</span>
    </div>
    <div class="peers">
      <div class="peer-greed">
        <div class="layer w-100 mB-20">
          <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('GALLERY')}} {{t('ABOUT')}} {{iname}}:</h6>
        </div>
        <div class="layer">
          <div ref="carousel" id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
            <ol class="carousel-indicators">
              <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
            </ol>
            <div class="carousel-inner">
              <div class="carousel-item active">
                <img class="d-block w-100" src="@/assets/static/images/gym-1.jpg" alt="First slide">
              </div>
              <div class="carousel-item">
                <img class="d-block w-100" src="@/assets/static/images/gym-2.jpg" alt="Second slide">
              </div>
              <div class="carousel-item">
                <img class="d-block w-100" src="@/assets/static/images/gym-3.jpg" alt="Third slide">
              </div>
            </div>
            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="sr-only">Previous</span>
            </a>
            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'GymGalley',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  },
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>
<style>

</style>



