<template>
  <tr>
    <td :class="{'fw-600': !rtl}">{{iname}}</td>
    <td>
      <span class="badge p-10 lh-0 tt-c badge-pill"
        :class="{
          'fw-300': rtl,
          'bgc-red-50 c-red-700': istatus === 'UNAVAILABLE',
          'bgc-yellow-50 c-yellow-700': istatus === 'OLD',
          'bgc-red-50 c-red-700': istatus === 'USED',
          'bgc-green-50 c-green-700': istatus === 'NEW'
        }">
          {{t(istatus)}}
      </span>
    </td>
    <td>Nov 20</td>
    <td>
      <span class="text-success">${{iprice}}</span>
    </td>
  </tr>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Purchase',
  props: {
    'iname': {
      type: String,
      required: true
    },
    'iprice': {
      type: Number,
      required: true
    },
    'itime': {
      type: Number,
      required: true
    },
    'istatus': {
      type: String,
      required: true,
      validator: function (value) {
        // The value must match one of these strings
        return ['UNAVAILABLE', 'NEW', 'USED', 'OLD'].indexOf(value) !== -1
      }
    }
  },
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



