<template>
  <div :class="{'rtl': rtl}">
    <!-- #Left Sidebar ==================== -->
    <g-sidebar></g-sidebar>

    <!-- #Main ============================ -->
    <g-main></g-main>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from './UserPage/Sidebar'
import Main from './UserPage/Main'
export default {
  name: 'UserPage',
  components: {
    'g-sidebar': Sidebar,
    'g-main': Main
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



