import Vue from 'vue'
import Router from 'vue-router'
import UserPage from '@/components/UserPage'
import UserGyms from '@/components/UserPage/main/UserGyms'
import UserPurchases from '@/components/UserPage/main/UserPurchases'
import UserProfile from '@/components/UserPage/main/UserProfile'
import UserAdminPage from '@/components/UserPage/main/UserAdminPage'
import Login from '@/components/Login'
import SignUp from '@/components/SignUp'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/user',
      name: 'UserPage',
      component: UserPage,
      children: [
        {
          path: 'gyms',
          component: UserGyms
        },
        {
          path: 'purchases',
          component: UserPurchases
        },
        {
          path: 'profile',
          component: UserProfile
        },
        {
          path: 'admin',
          component: UserAdminPage
        }
      ]
    }, {
      path: '/login',
      component: Login
    }, {
      path: '/signup',
      component: SignUp
    }, {
      path: '/',
      redirect: '/user/gyms'
    }
  ]
})



