<template>
  <div class="peers fxw-nw" :class="{'ai-fe': istatus === 'OTHERS'}">
    <div class="peer" :class="{
        'mR-20': (!rtl && istatus === 'ME' || rtl && istatus === 'OTHERS'),
        'mL-20': (rtl && istatus === 'ME' || !rtl && istatus === 'OTHERS'),
        'ord-1': (istatus === 'OTHERS')
      }">
      <img class="w-2r bdrs-50p" src="https://randomuser.me/api/portraits/men/11.jpg" alt="">
    </div>
    <div class="peer peer-greed" :class="{'ai-fe': istatus === 'OTHERS',}">
      <div class="layers gapY-5" :class="{
