                'ord-1': (istatus === 'OTHERS')
              }">
              <small>10:00</small>
            </div>
            <div class="peer-greed" :class="{'ord-0': (istatus === 'OTHERS')}">
              <span>Lorem Ipsum has been the industry's</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'comment',
  components: {
  },
  props: {
    iimg: {
      type: String,
      required: true
    },
    istatus: {
      type: String,
      required: true,
      validator: function (value) {
        // The value must match one of these strings
        return ['OTHERS', 'ME'].indexOf(value) !== -1
      }
    },
    imessages: {
      type: Array,
      required: true,
      validator: function (value) {
        // The value must match one of these strings
        return value[0].messages.length
      }
    }
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



