<template>
  <!-- Search -->
  <div class="bdB layer w-100" :class="{'rtl': rtl}">
    <input type="text" :placeholder="t('SEARCH_GYMS') + '...'" name="chatSearch" class="form-constrol p-15 bdrs-0 w-100 bdw-0">
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Search',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



