<template>
  <!-- ### $Sidebar Header ### -->
  <div class="sidebar-logo" ref="SidebarHeader">
    <div class="peers ai-c fxw-nw">
      <div class="peer peer-greed">
        <a class="sidebar-link td-n" href="#/user/gyms">
          <div class="peers ai-c fxw-nw">
            <div class="peer">
              <div class="logo">
                <img src="@/assets/static/images/logo.png" alt="">
              </div>
            </div>
            <div class="peer peer-greed">
              <h5 class="lh-1 mB-0 logo-text" :class="{'ta-r': rtl}">Mehrdadinator</h5>
            </div>
          </div>
        </a>
      </div>
      <div class="peer">
        <div class="mobile-toggle pX-20 sidebar-toggle">
          <a href="#!" class="td-n" @click="userPageApplicationSidebarManage({type: 'toggle'})">
            <i class="ti-arrow-circle-left"></i>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'SidebarHeader',
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  methods: {
    ...mapActions([
      'userPageApplicationSidebarManage'
    ])
  },
  data () {
    return {
    }
  },
  mounted () {
  }
}
</script>



