<script>
import { mapGetters } from 'vuex'
export default {
  name: 'GymHours',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



