<template>
  <div class="bd p-20 gapY-15 bg-white" :class="{'rtl': rtl}">
    <div class="layer w-100 mB-10">
      <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('COMMENTS')}} {{t('ABOUT')}} {{iname}}:</h6>
    </div>
    <!-- Chat Conversation -->
    <!-- my comment -->
    <g-comment
      :iimg="'https://randomuser.me/api/portraits/men/11.jpg'"
      :istatus="'ME'"
      :imessages="[{messages: [{}]}]">
    </g-comment>
    <!-- other comments -->
    <g-comment
      :iimg="'https://randomuser.me/api/portraits/men/12.jpg'"
      :istatus="'OTHERS'"
      :imessages="[{messages: [{}]}]">
    </g-comment>
    <!-- Chat Conversation -->
    <say-comment>
    </say-comment>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import Comment from './GymPeopleComments/Comment'
import SayComment from './GymPeopleComments/SayComment'
export default {
  name: 'GymPeopleComments',
  components: {
    'g-comment': Comment,
    'say-comment': SayComment
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



