<template>
  <div class="layer w-100">
    <div class="bgc-white">
      <div class="pos-r">
        <input type="text" class="form-control bdrs-10em m-0" :placeholder="t('SAY_SOMETHING') + '...'">
        <button type="button" class="btn btn-primary bdrs-50p w-2r p-0 h-2r pos-a t-1" :class="{'r-1': !rtl, 'l-1': rtl}">
          <i class="fa fa-paper-plane-o">
          </i>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'SayComment',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



