<template>
  <li class="dropdown" ref="user" :class="{'rtl': rtl}">
    <a href="" class="dropdown-toggle no-after peers fxw-nw ai-c lh-1" data-toggle="dropdown">
      <div class="peer" :class="{'rtl': rtl, 'mR-10': !rtl, 'mL-10': rtl}">
        <img class="w-2r bdrs-50p" src="https://randomuser.me/api/portraits/men/10.jpg" alt="">
      </div>
      <div class="peer">
        <span class="fsz-sm c-grey-900">{{iname}}</span>
      </div>
    </a>
    <ul class="dropdown-menu fsz-sm">
      <li>
        <a href="#/user/profile" class="d-b td-n pY-5 bgcH-grey-100 c-grey-700" :class="{'ta-r': rtl}">
          <i class="ti-user va-m" :class="{'mR-10': !rtl, 'mL-10': rtl}"></i>
          <span>{{t('PROFILE')}}</span>
        </a>
      </li>
      <li>
        <a href="email.html" class="d-b td-n pY-5 bgcH-grey-100 c-grey-700" :class="{'ta-r': rtl}">
          <i class="ti-email va-m" :class="{'mR-10': !rtl, 'mL-10': rtl}"></i>
          <span>{{t('PURCHASES')}}</span>
        </a>
      </li>
      <li role="separator" class="divider"></li>
      <li>
        <a href="" class="d-b td-n pY-5 bgcH-grey-100 c-grey-700" :class="{'ta-r': rtl}">
          <i class="ti-power-off va-m" :class="{'mR-10': !rtl, 'mL-10': rtl}"></i>
          <span>{{t('LOGOUT')}}</span>
        </a>
      </li>
    </ul>
  </li>
</template>
<script>
import { mapGetters } from 'vuex'
import * as $ from 'jquery'
export default {
  name: 'User',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  },
  data () {
    return {
    }
  },
  mounted () {
    this.$nextTick(function () {
      $(this.$refs.user).find('.dropdown-toggle').click(function (e) {
        $(this).parent().toggleClass('show')
        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



