<template>
  <div class="layer w-100 mY-30" :class="{'rtl': rtl}">
    <div class="layers bdB">
      <div class="layer w-100 bdT pY-5">
        <div class="peers ai-c jc-sb fxw-nw">
          <div class="peer">
            <span>{{t('SHOWERS')}}</span>
          </div>
          <div class="peer ta-r">
            <span class="fw-600 c-grey-800">
              <i class="c-green-500  ti-check fsz-md"></i>
            </span>
          </div>
        </div>
      </div>
      <div class="layer w-100 bdT pY-5">
        <div class="peers ai-c jc-sb fxw-nw">
          <div class="peer">
            <span>{{t('CHANGING_ROOM')}}</span>
          </div>
          <div class="peer ta-r">
            <span>
              <i class="c-green-500  ti-check fsz-md"></i>
            </span>
          </div>
        </div>
      </div>
      <div class="layer w-100 bdT pY-5">
        <div class="peers ai-c jc-sb fxw-nw">
          <div class="peer">
            <span>{{t('LOCKERS')}}</span>
          </div>
          <div class="peer ta-r">
            <span class="fw-600 c-grey-800">
              <i class="c-red-500 ti-close fsz-md"></i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'GymFacilities',
  components: {
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  }
}
</script>
<style>

</style>



